import './carouselcomponent.css'

const CarouselComponent = () => {
    return (
      <div className="carousel_component">
        <div className="carousel_container">
          <div className="title_container">
              <h1>Spotpris</h1>
          </div>
          <div className="content_container">
            <div className="left_container">
              <p>Markedsprisen p strm endrer seg gjennom ret, og strmregningen din vil variere etter disse svingningene. </p>
              <p>Du m forvente hyere pris i vinterhalvret, men kan nyte godt av lave strmpriser om sommeren. </p>
              <p>Spotpris har vist seg  vre billigst i gjennomsnitt de siste rene.</p>
            </div>
            <div className="right_container">
              <img src="https://via.placeholder.com/300x300" alt="placeholder"/>
            </div>
          </div>
        </div>
      </div>
    )
};

export default CarouselComponent;